<template>
  <div>
    <v-menu offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          v-bind="attrs"
          v-on="on"
          icon
        >
          <v-icon color="grey darken-1">mdi-dots-vertical</v-icon>
        </v-btn>
      </template>
      <v-list>
        <v-list-item
          v-for="(item, index) in items"
          :key="index"
          @click="item.click()"
        >
          <v-icon left>{{ item.icone }}</v-icon>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    <ModalEditar
     v-if="items[0].modal"
     @fechaModal="items[0].modal = false"
     :tarefa="tarefa"
    />
    <ModalDelete
     v-if="items[1].modal"
     @fechaModal="items[1].modal = false"
     :tarefa="tarefa"
    />
  </div>
</template>

<script>
import ModalDelete from '../modal/ModalDelete.vue'
import ModalEditar from '../modal/ModalEditar.vue'
  export default {
    components: { ModalEditar, ModalDelete },
    props: ['tarefa'],
    data: () => ({
      items:[
        { 
          icone: 'mdi-pencil', 
          title: 'Editar',
          modal: false,
          click(){
            this.modal=true
          } 
        },

        { 
          icone: 'mdi-delete',
          title: 'Excluir',
          modal: false,
          click(){
            this.modal=true            
          }  
        },
      ],
    }),
  }
</script>

